import { VehiculotalleredosEditModalComponent } from './../../../vehiculotalleredos/components/vehiculotalleredos-table/vehiculotalleredos-edit-modal/vehiculotalleredos-edit-modal.component';
import { VehiculotallersSalidaEditModalComponent } from './vehiculotallerssalida-edit-modal/vehiculotallerssalida-edit-modal.component';
import { SalidastocksModule } from './../../../salidastocks/salidastocks.module';
import { AlertasService } from './../../../alertas/components/alertas-table/alertas.service';
import { AlertasInterface } from './../../../alertas/components/alertas-table/alertas.interface';
import { SocketService } from './../../../../shared/socket.service';
import { PermisotaxiestadosService } from './../../../permisotaxiestados/components/permisotaxiestados-table/permisotaxiestados.service';
import { AuthLocalstorage } from './../../../../shared/auth-localstorage.service';
import { VehiculotalleredosService } from './../../../vehiculotalleredos/components/vehiculotalleredos-table/vehiculotalleredos.service';
import { OrdensService } from './../../../ordens/components/ordens-table/ordens.service';
import { AuthService } from './../../../../shared/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DialogService } from 'ng2-bootstrap-modal';
import { ToastrService } from 'ngx-toastr';
import { VehiculotallersInterface } from './vehiculotallers.interface';
import { VehiculotallersResponseInterface } from './vehiculotallers-response.interface';
import { Component, OnInit } from '@angular/core';
import { VehiculotallersService } from './vehiculotallers.service';
import { VehiculotallersAddModalComponent } from './vehiculotallers-add-modal/vehiculotallers-add-modal.component';
import { VehiculotallersEditModalComponent } from './vehiculotallers-edit-modal/vehiculotallers-edit-modal.component';
import { OrdensInterface } from './../../../ordens/components/ordens-table/ordens.interface';
import { OrdensAddModalComponent } from './../../../ordens/components/ordens-table/ordens-add-modal/ordens-add-modal.component';
import { VehiculotalleredosInterface } from './../../../vehiculotalleredos/components/vehiculotalleredos-table/vehiculotalleredos.interface';
import { VehiculotalleredosAddModalComponent } from './../../../vehiculotalleredos/components/vehiculotalleredos-table/vehiculotalleredos-add-modal/vehiculotalleredos-add-modal.component';
import { PermisotaxisService } from './../../../permisotaxis/components/permisotaxis-table/permisotaxis.service';
import { PermisotaxisInterface } from './../../../permisotaxis/components/permisotaxis-table/permisotaxis.interface';

@Component({
selector: 'vehiculotallers-table',
templateUrl: './vehiculotallers-table.html',
styleUrls: ['./vehiculotallers-table.scss'],
})
export class VehiculotallersTableComponent implements OnInit {
    data;
    filterQuery = '';
    rowsOnPage = 10;
    sortBy = 'idvehiculotaller';
    sortOrder = 'asc';
    backpage: boolean;

    cajaTrabajando;
    cajaPorHacer;
    cajaHecha;
    cajaSalida;

    fecha;
    hora;

    porhacerse = [];
    trabajando = [];
    hecha = [];

    // Permisos en vista
    updateable: boolean = false;
    deleteable: boolean = false;
    writeable: boolean = false;

    constructor(
      private service: VehiculotallersService, 
      private toastrService: ToastrService, 
      private dialogService: DialogService, 
      private permisotaxisService: PermisotaxisService,
      private authLocalstorage: AuthLocalstorage,
      private vehiculotalleredosService: VehiculotalleredosService,
      private permisotaxiestadosService: PermisotaxiestadosService,
      private socketService: SocketService,
      private ordensService: OrdensService,
      private authService: AuthService, 
      private route: ActivatedRoute, 
      private alertasService: AlertasService,
      private router: Router) {

      // Buscar permisos del usuario en el mdulo
      const user = this.authService.useJwtHelper();
      
      if (user.super) {
        this.updateable = true;
        this.deleteable = true;
        this.writeable = true;
      } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
          for (const element in userModules) {
            if (userModules[element].path === '/pages/vehiculotallers') {
              this.updateable = userModules[element].updateable;
              this.deleteable = userModules[element].deleteable;
              this.writeable = userModules[element].writeable;
            }
          }
        }
      }
    }
    ngOnInit() {
      this.refill();
      this.iniciarDragAndDrop();      
      
      // FECHA Y HORA ACTUAL
      const date = this.authLocalstorage.getCurrentDateAndHour();
      this.fecha = date.fecha;
      this.hora = date.hora;
    }
    refill() { 
      this.route.params.subscribe(params => {
        if (params['idempleado'] !== undefined) {
          const idempleado = +params['idempleado'];
          this.findByIdEmpleado(idempleado);
          this.backpage = true;
        }
        if (params['idestado'] !== undefined) {
          const idestado = +params['idestado'];
          this.findByIdEstado(idestado);
          this.backpage = true;
        }
        if (params['idtaller'] !== undefined) {
          const idtaller = +params['idtaller'];
          this.findByIdTaller(idtaller);
          this.backpage = true;
        }
        if (params['idvehiculo'] !== undefined) {
          const idvehiculo = +params['idvehiculo'];
          this.findByIdVehiculo(idvehiculo);
          this.backpage = true;
        }
        if (!this.backpage) {
          this.getAll();
        }
      });
    }

    expandMessage (item) {
      item.expanded = !item.expanded;
    }

    iniciarDragAndDrop() { 
        this.cajaTrabajando = document.getElementById('cajaTrabajando');  
        this.cajaHecha = document.getElementById('cajaHecha');  
        this.cajaPorHacer = document.getElementById('cajaPorHacer');  
        this.cajaSalida = document.getElementById('cajaSalida');  

        this.cajaTrabajando.addEventListener('dragenter', function(e) { e.preventDefault(); }, false);  
        this.cajaTrabajando.addEventListener('dragover', function(e) { e.preventDefault(); }, false);  

        this.cajaHecha.addEventListener('dragenter', function(e) { e.preventDefault(); }, false);  
        this.cajaHecha.addEventListener('dragover', function(e) { e.preventDefault(); }, false);  

        this.cajaPorHacer.addEventListener('dragenter', function(e) { e.preventDefault(); }, false);  
        this.cajaPorHacer.addEventListener('dragover', function(e) { e.preventDefault(); }, false); 

        this.cajaSalida.addEventListener('dragenter', function(e) { e.preventDefault(); }, false);  
        this.cajaSalida.addEventListener('dragover', function(e) { e.preventDefault(); }, false);  
    }

    finalizado(e) { 
        const elemento = e.target; 
    } 
    
    arrastrado(e) { 
        const elemento = e.target;  
        const idelemento = elemento.getAttribute('id');
        const id = elemento.getAttribute('id').split(',')[0];
        const estado = elemento.getAttribute('id').split(',')[1];

        let idvehiculo = undefined;
        if (idelemento.split(',')[2]) {
          idvehiculo = idelemento.split(',')[2];
          e.dataTransfer.setData('idvehiculo', idvehiculo);  
        }
        let idempleado = undefined;
        if (idelemento.split(',')[3]) {
          idempleado = idelemento.split(',')[3];
          e.dataTransfer.setData('idempleado', idempleado);  
        }
        let motivo = undefined;
        if (idelemento.split(',')[4]) {
          motivo = idelemento.split(',')[4];
          e.dataTransfer.setData('motivo', motivo);  
        }
        
        e.dataTransfer.setData('idelemento', idelemento);  
        e.dataTransfer.setData('id', id);  
        e.dataTransfer.setData('estado', estado);  
        e.dataTransfer.setDragImage(e.target, 0, 0); 
    } 

    soltadoPorHacer(e) { 
        e.preventDefault();  
        const idvehiculotaller = e.dataTransfer.getData('id'); 
        const estado = e.dataTransfer.getData('estado'); 
        const elemento = document.getElementById(idvehiculotaller); 
        document.getElementById('cajaPorHacer').appendChild(elemento);
        this.cambiarEstado(+idvehiculotaller, 1);
    }

    soltadoTrabajando(e): void { 
        e.preventDefault();  
        const idelemento = e.dataTransfer.getData('idelemento'); 
        const idvehiculotaller = e.dataTransfer.getData('id');
        const estado = e.dataTransfer.getData('estado');   

        if (estado === 'POR HACERSE') {
          const vehiculotaller = {
              'idvehiculotaller': +idvehiculotaller,
          };
          this.insertOrden(vehiculotaller, idelemento);
        }
    }

    soltadoHecha(e) { 
        e.preventDefault();  
        const idelemento = e.dataTransfer.getData('idelemento'); 
        const idvehiculotaller = e.dataTransfer.getData('id');  
        const estado = e.dataTransfer.getData('estado');  
        const elemento = document.getElementById(idelemento); 
        document.getElementById('cajaHecha').appendChild(elemento);
        this.cambiarEstado(+idvehiculotaller, 3);
    }

    soltadoSalida(e) { 
        e.preventDefault();  
        const idelemento = e.dataTransfer.getData('idelemento'); 
        const idvehiculotaller = e.dataTransfer.getData('id');  
        const estado = e.dataTransfer.getData('estado');  
        const idempleado = e.dataTransfer.getData('idempleado');  
        const idvehiculo = e.dataTransfer.getData('idvehiculo'); 
        const motivo = e.dataTransfer.getData('motivo'); 

        console.log("idvehiculo", idvehiculo);

        // OBTENER EL REGISTRO DE PERMISOTAXI QUE TIENE EL IDVEHICULO
        this.permisotaxisService.findByIdVehiculo(idvehiculo)
          .subscribe((data: any) => {

          // REVISAR ESTADO DE Ordens INCLUYENDO BOOLEANO SI ORDENTAREA ESTN TODAS VALIDADAS
          this.ordensService.findByIdVehiculotaller(idvehiculotaller, idvehiculo).subscribe(
            (ordens) => {

              if (ordens) {

                if (ordens.success) {
                  if (ordens.result[0].estado_estado_idestado === 'PAGADO SIN ENTREGAR' || 
                      ordens.result[0].estado_estado_idestado === 'PAGADO ENTREGADO' ||
                      ordens.result[0].estado_estado_idestado === 'FINALIZADO' ||
                      ordens.result[0].estado_estado_idestado === 'SOBREPAGADO' ||
                      ordens.result[0].estado_estado_idestado === 'SINCOSTO') {


                      // MOSTRAR MODAL DE EDICION DE VEHICULOTALLER PARA ESTABLECER FECHA Y HORA DE SALIDA  
                      const vehiculotaller = {
                                vehiculotaller_idvehiculotaller: idvehiculotaller,
                                estadoscrum_idestadoscrum: 4,
                                fecha: this.fecha,
                                hora: this.hora,
                      };
                      this.addEstadoSalidaModalShow(vehiculotaller);


                      const alerta: AlertasInterface = {
                          empleado_idempleado: +idempleado,
                          tipoalerta_idtipoalerta: 2, // AVISO
                          mensaje: `Ha salido un vehculo de ${motivo}, porfavor revsalo.|${idvehiculo}`,
                      };
                      // CREAR UNA Alerta
                      this.alertasService.insert(alerta)
                          .subscribe((_data: any) => {
                              if (_data.success) {
                                  this.toastrService.success(_data.message);
                                  
                                  // ENVIAR A WEBSOCKET
                                  this.socketService.sender(JSON.stringify(alerta));

                                  // LA ORDEN YA EST PAGADA
                                  this.cambiarPermisoEstado(+ordens.result[0].idpermisotaxi, ((motivo === 'MANTENIMIENTO') ? 14 : 11)); // MANTENIMIENTO DISPONIBLE : TALLER DISPONIBLE

                                  const elemento = document.getElementById(idelemento); 
                                  document.getElementById('cajaSalida').appendChild(elemento);
                              } else {
                                  this.toastrService.error(_data.message);
                              }
                          });

                  } else {
                    this.toastrService.error('Para poder dar salida al vehiculo primero debe ser pagada la orden correspondiente');
                  } 
                } else {
                    this.toastrService.error(ordens.message);
                }

              } else {
                this.toastrService.error('No existe una orden para este servicio, por favor, agregue una');
              }

            });

          });
    }
  
    private cambiarPermisoEstado(id: number, estadoactividad: number): void {
        this.permisotaxiestadosService
          .insert({
                    'permisotaxi_idpermisotaxi': id,
                    'estadoactividad_idestadoactividad': estadoactividad,
                    'fecha': this.fecha,
                    'hora': this.hora,
          })
          .subscribe(
              (data: any) => {
                this.showToast(data);
              },
              error => console.log(error),
              () => console.log('change permisotaxiestado complete'));
    }

    private cambiarEstado(idvehiculotaller: number, estadoscrum: number): void {
      this.vehiculotalleredosService
        .insert({
                  vehiculotaller_idvehiculotaller: idvehiculotaller,
                  estadoscrum_idestadoscrum: estadoscrum,
                  fecha: this.fecha,
                  hora: this.hora,
        })
        .subscribe(
            (data: any) => {
              this.showToast(data);
            },
            error => console.log(error),
            () => console.log('insertTrabajando complete'))
    }

    filtrarResultados(data) {

          const porhacerse = [];
          const trabajando = [];
          const hecha = [];

          if (data.success) {
              if (data.result[0]) {
                for (const element in data.result) {
                  if (data.result.hasOwnProperty(element)) {

                    if (data.result[element].estado_estado_idestado === 'POR HACERSE') {
                      porhacerse.push(data.result[element]);
                    } else if (data.result[element].estado_estado_idestado === 'TRABAJANDO') {
                      trabajando.push(data.result[element]);
                    } else if (data.result[element].estado_estado_idestado === 'HECHA') {
                      hecha.push(data.result[element]);
                    }
                    if (+element + 1 === data.result.length) {
                      this.porhacerse = porhacerse;
                      this.trabajando = trabajando;
                      this.hecha = hecha;
                    }

                  }
                }

                this.data = data.result;

            } else {
              this.data = [];
            }

        } else {
          this.toastrService.error(data.message);
        }
    }

    private findByIdEmpleado(id: number): void {
      this.service
        .findByIdEmpleado(id)
        .subscribe(
            (data: VehiculotallersResponseInterface) => {
                this.filtrarResultados(data);
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }
    private findByIdEstado(id: number): void {
      this.service
        .findByIdEstado(id)
        .subscribe(
            (data: VehiculotallersResponseInterface) => {
                this.filtrarResultados(data);
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }
    private findByIdTaller(id: number): void {
      this.service
        .findByIdTaller(id)
        .subscribe(
            (data: VehiculotallersResponseInterface) => {
                this.filtrarResultados(data);
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }
    private findByIdVehiculo(id: number): void {
      this.service
        .findByIdVehiculo(id)
        .subscribe(
            (data: VehiculotallersResponseInterface) => {
                this.filtrarResultados(data);
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }
    backPage() {
        window.history.back();
    }

    insertOrden(vehiculotallers: VehiculotallersInterface, idelemento: string) {
      const orden: OrdensInterface = {
        vehiculotaller_idvehiculotaller: vehiculotallers.idvehiculotaller
      }
      const disposable = this.dialogService.addDialog(OrdensAddModalComponent, orden)
      .subscribe( data => {
          if (data) {
              this.ordenShowToast(data);

              const elemento = document.getElementById(idelemento);
              document.getElementById('cajaTrabajando').appendChild(elemento);
              this.cambiarEstado(vehiculotallers.idvehiculotaller, 2);


          }
      });
    }
    ordenShowToast(result) {
        if (result.success) {
            this.toastrService.success(result.message);
            this.refill();
        } else {
            this.toastrService.error(result.message);
        }
    }
    viewOrden(vehiculotallers: VehiculotallersInterface) {
      this.router.navigate([`/pages/ordens/vehiculotaller/${vehiculotallers.idvehiculotaller}/${vehiculotallers.vehiculo_idvehiculo}`]);
    }
    insertVehiculotalleredo(vehiculotallers: VehiculotallersInterface) {
      const vehiculotalleredo: VehiculotalleredosInterface = {
        vehiculotaller_idvehiculotaller: vehiculotallers.idvehiculotaller
      };
      const disposable = this.dialogService.addDialog(VehiculotalleredosAddModalComponent, vehiculotalleredo)
      .subscribe( data => {
          if (data) {
              this.vehiculotalleredoShowToast(data);
          }
      });
    }
    vehiculotalleredoShowToast(result) {
        if (result.success) {
            this.toastrService.success(result.message);
            this.refill();
        } else {
            this.toastrService.error(result.message);
        }
    }

    viewVehiculotalleredo(vehiculotallers: VehiculotallersInterface) {
      this.router.navigate([`/pages/vehiculotalleredos/vehiculotaller/${vehiculotallers.idvehiculotaller}`]);
    }
    addModalShow() {
      const disposable = this.dialogService.addDialog(VehiculotallersAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.showToast(data);
          }
      });
    }
    editModalShow(vehiculotallers: VehiculotallersInterface) {
      const disposable = this.dialogService.addDialog(VehiculotallersEditModalComponent, vehiculotallers)
      .subscribe( data => {
          if (data) {
              this.showToast(data);
          }
      },
      error => console.log(error),
      () => console.log('Modified complete'));
    }



    addEstadoSalidaModalShow(idvehiculotaller: VehiculotalleredosInterface) {
      const disposable = this.dialogService.addDialog(VehiculotalleredosAddModalComponent, idvehiculotaller)
      .subscribe( data => {
          if (data) {
              this.showToast(data);
          }
      },
      error => console.log(error),
      () => console.log('Modified complete'));
    }

    editSalidaModalShow(idvehiculotaller: VehiculotallersInterface) {
      const disposable = this.dialogService.addDialog(VehiculotallersSalidaEditModalComponent, idvehiculotaller)
      .subscribe( data => {
          if (data) {
              this.showToast(data);
          }
      },
      error => console.log(error),
      () => console.log('Modified complete'));
    }

    
    onDeleteConfirm(event, item): void {
      if (window.confirm('Estas seguro de querer eliminar este registro?')) {
          this.service.remove(item.idvehiculotaller)
          .subscribe(
              (data) => this.showToast(data),
              error => console.log(error),
              () => console.log('Delete completed')
          );
      } else {
          console.log('item cancelado');
      }
    }
    showToast(result) {
      if (result.success) {
        this.toastrService.success(result.message);
        this.refill();
      } else {
        this.toastrService.error(result.message);
      }
    }
    private getAll(): void {
      this.service
        .all()
        .subscribe(
            (data: VehiculotallersResponseInterface) =>  {
                this.filtrarResultados(data);
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    } 
  }



