import { VehiculotallersSalidaEditModalComponent } from './components/vehiculotallers-table/vehiculotallerssalida-edit-modal/vehiculotallerssalida-edit-modal.component';
import { BootstrapModalModule } from 'ng2-bootstrap-modal';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule as AngularFormsModule, ReactiveFormsModule } from '@angular/forms';
import { AppTranslationModule } from '../../app.translation.module';
import { NgaModule } from '../../theme/nga.module';
import { NgbRatingModule } from '@ng-bootstrap/ng-bootstrap';
import { DataTableModule } from 'angular2-datatable';
import { NgbModalModule } from '@ng-bootstrap/ng-bootstrap';
import { routing } from './vehiculotallers.routing';
import { VehiculotallersComponent } from './vehiculotallers.component';
import { VehiculotallersService } from './components/vehiculotallers-table/vehiculotallers.service';
import { VehiculotallersTableComponent } from './components/vehiculotallers-table/vehiculotallers-table.component';

@NgModule({
  imports: [
    CommonModule,
    AngularFormsModule,
    AppTranslationModule,
    ReactiveFormsModule,
    NgaModule,
    NgbRatingModule,
    routing,
    DataTableModule,
    NgbModalModule,
    BootstrapModalModule.forRoot({ container: document.body })
  ],
  declarations: [
    VehiculotallersComponent,
    VehiculotallersTableComponent,
    VehiculotallersSalidaEditModalComponent,
  ],
  entryComponents: [
    VehiculotallersSalidaEditModalComponent,
  ],
  providers: [
    VehiculotallersService,
  ]
})
export class VehiculotallersModule {
}



