import { AuthService } from './../../../../../shared/auth.service';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { AuthLocalstorage } from './../../../../../shared/auth-localstorage.service';
import { VehiculotallersService } from './../vehiculotallers.service';
import { VehiculotallersInterface } from './../vehiculotallers.interface';
import { Component, OnInit } from '@angular/core';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { EstadosService } from './../../../../estados/components/estados-table/estados.service';
import { EstadosAddModalComponent } from './../../../../estados/components/estados-table/estados-add-modal/estados-add-modal.component';
import { TallersService } from './../../../../tallers/components/tallers-table/tallers.service';
import { TallersAddModalComponent } from './../../../../tallers/components/tallers-table/tallers-add-modal/tallers-add-modal.component';
import { EmpleadosService } from './../../../../empleados/components/empleados-table/empleados.service';
import { EmpleadosAddModalComponent } from './../../../../empleados/components/empleados-table/empleados-add-modal/empleados-add-modal.component';
import { VehiculosService } from './../../../../vehiculos/components/vehiculos-table/vehiculos.service';
import { VehiculosAddModalComponent } from './../../../../vehiculos/components/vehiculos-table/vehiculos-add-modal/vehiculos-add-modal.component';
@Component({
  selector: 'edit-service-modal',
  styleUrls: [('./vehiculotallerssalida-edit-modal.component.scss')],
  templateUrl: './vehiculotallerssalida-edit-modal.component.html'
})
export class VehiculotallersSalidaEditModalComponent extends DialogComponent<VehiculotallersInterface, any> implements OnInit, VehiculotallersInterface {

  idvehiculotaller: number;
  fechaSalida: string;
  horaSalida: string;

estadoRewriteable: boolean = false;
tallerRewriteable: boolean = false;
empleadoRewriteable: boolean = false;
vehiculoRewriteable: boolean = false;

  modalHeader: string;
  data: any;
  form: FormGroup;
  submitted: boolean = false;

  fechaSalidaAC: AbstractControl;
  horaSalidaAC: AbstractControl;

  constructor(
      private service: VehiculotallersService,
      private authService: AuthService, 
      private estadosService: EstadosService,
      private tallersService: TallersService,
      private empleadosService: EmpleadosService,
      private vehiculosService: VehiculosService,
      fb: FormBuilder,
      private toastrService: ToastrService,
      private authLocalstorage: AuthLocalstorage,
      dialogService: DialogService,
  ) {
  super(dialogService);
  this.form = fb.group({
    'fechaSalidaAC' : [''],
    'horaSalidaAC' : [''],
  });
  this.fechaSalidaAC = this.form.controls['fechaSalidaAC'];
  this.horaSalidaAC = this.form.controls['horaSalidaAC'];
    // Buscar permisos del usuario en el mdulo
    const user = this.authService.useJwtHelper();

    if (user.super) {
        this.estadoRewriteable = true;
        this.tallerRewriteable = true;
        this.empleadoRewriteable = true;
        this.vehiculoRewriteable = true;
    } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
            for (const element in userModules) {
                if (userModules.hasOwnProperty(element)) {
                    if (userModules[element].path === '/pages/estados') {
                        this.estadoRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/tallers') {
                        this.tallerRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/empleados') {
                        this.empleadoRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/vehiculos') {
                        this.vehiculoRewriteable = userModules[element].writeable;
                    }
                } 
            }
        }
    }
  }
  ngOnInit() {
  }
  
  confirm() {
      this.result = this.data;
      this.close();
  }
  onSubmit(values: VehiculotallersInterface): void {
      this.submitted = true;
      if (this.form.valid) {

          console.log("this.idvehiculotaller", this.idvehiculotaller);
          this.service
              .update({
                  idvehiculotaller: this.idvehiculotaller,
                  fechaSalida: this.fechaSalida,
                  horaSalida: this.horaSalida,
              })
              .subscribe(
                  (data: any) => {
                      this.data = data;
                      this.confirm();
              });
          }
  }
}



