import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import {VehiculotallersComponent } from './vehiculotallers.component';
import {VehiculotallersTableComponent } from './components/vehiculotallers-table/vehiculotallers-table.component';
// noinspection TypeScriptValidateTypes
export const routes: Routes = [
  {
    path: '',
    component: VehiculotallersComponent,
    children: [
      { path: 'vehiculotallers-table', component: VehiculotallersTableComponent }
    ]
    }
  ];
export const routing: ModuleWithProviders = RouterModule.forChild(routes);



